$breakpoint-sm: 768px;

:root {
  --color-black: #000000;
  --color-white: #ffffff;
  --color-white-transparent: rgba(255, 255, 255, 0.5);

  --color-success: #5cb85c;

  --color-card-background: #2e2e2e60;
  --color-card-border: #3c3c3c;

  --color-popup-background: linear-gradient(to bottom, #210000, #150000);
  --color-progress-bar-background: linear-gradient(to right, #4b134f, #c94b4b);

  --size-xs: 4px;
  --size-sm: 8px;
  --size-md: 16px;
  --size-lg: 32px;
  --size-xl: 48px;
  --size-2xl: 96px;

  --font-size-xs: 12px;
  --font-size-sm: 14px;
  --font-size-md: 16px;
  --font-size-lg: 20px;
  --font-size-xl: 30px;
  --font-size-2xl: 46px;

  --icon-size: 20px;

  --container-size-lg: 940px;
}
